@import './settings.scss';
@import './mixins.scss';

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#root {
  margin-top: $header-height;
  margin-bottom: $footer-height;
}

html, body #root {
  height: 100%;
  min-height: 100%;
  background-color: lighten($color-grey, 25%);
}
